<template>
  <div class="container">
    <div class="find-content">
      <div class="find-sort">
        <h6 class="find-label">类型：</h6>
        <div class="find-sort-wrap">
          <div class="find-sort-item"
               v-for="(sort,i) in sortList"
               :key="i" @click="getSortName($event)">
            <MovieSort :type="sort.type"
                       :name="sort.name"></MovieSort>
          </div>
        </div>
      </div>
    </div>
    <footer>
      <span>已经到底了哟</span>
    </footer>
  </div>
</template>

<script>
import MovieSort from "../components/MovieSort";

export default {
  name: "Find",
  data() {
    return {
      sortList: [
        {
          type: "love",
          name: "爱情"
        },
        {
          type: "comedy",
          name: "喜剧"
        },
        {
          type: "sci-fi",
          name: "科幻"
        },
        {
          type: "anime",
          name: "动画"
        },
        {
          type: "crime",
          name: "犯罪"
        },
        {
          type: "documentary",
          name: "纪录片"
        },
        {
          type: "suspense",
          name: "悬疑"
        },
        {
          type: "song-and-dance",
          name: "歌舞"
        },
        {
          type: "homosexual",
          name: "同性"
        },
        {
          type: "fantasy",
          name: "奇幻"
        },
        {
          type: "action-adventure",
          name: "动作"
        }
      ]
    };
  },
  methods: {
    getSortName(e) {
      /**
       * @e event MovieSort 父级元素
       * 功能：
       * 点击卡片获取其中的 name 作为搜索关键字。
       */
      let name =e.toElement.innerText;
      this.$router.push({
        name: 'Search',
        params:{
          name: name
        }
      })
    }
  },
  components: {
    MovieSort
  }
};
</script>

<style scoped>
.container {
  display: flex;
  box-sizing: border-box;
  padding-top: 63px;
  flex-flow: column;
  min-height: 100vh;
  line-height: 1;
}
p {
  margin: 0;
}
h6 {
  margin: 0;
  padding: 0;
}
.find-content {
  padding: 0 5px;
  flex: 1;
}
.find-label {
  margin-bottom: 10px;
  font-size: 16px;
}
.find-sort-wrap {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
footer {
  flex: 0;
}
</style>


