<template>
  <div class="search-page">
    <div class="search-bar">
      <form action="/">
        <van-search v-model="$store.state.searchValue"
                    placeholder="请输入搜索关键词"
                    show-action
                    @search="onSearch"
                    @cancel="onCancel"
                    @focus="focus"
                    v-on:input="onReset"
                    ref="searchBox" />
      </form>
    </div>
    <div class="search-tip container"
         v-show="tipShow">
      <div class="suggest suggest-search">
        <div class="history"
             v-show="$store.state.historySearch.length != 0">
          <h6 class="search-label">最近搜索：</h6>
          <div class="suggest-item">
            <span v-for="(history,j) in $store.state.historySearch"
                  :key="j"
                  @click="searchItem($event)">{{history}}</span>
          </div>
        </div>
        <h6 class="search-label">推荐搜索：</h6>
        <div class="suggest-item">
          <span v-for="(item,i) in suggestList"
                :key="i"
                @click="searchItem($event)">{{item}}</span>
        </div>
      </div>
    </div>
    <div class="search-content container"
         v-show="!tipShow">
      <h6 class="search-label">以下是<span class="keyword">{{$store.state.searchValue}}</span>的搜索结果</h6>
      <div class="search-movie-card"
           v-for="(movie,i) in searchData"
           :key="i">
        <router-link :to="'/movieInfo/'+ movie.id">
          <img class="movie-card-cover"
               :src="movie.images.small"
               :alt="movie.title"
               v-lazy="movie.images.small">
          <div class="movie-text-info">
            <h6 class="movie-title">{{movie.title}}</h6>
            <p class="movie-dir">导演：<span v-for="(dir,j) in movie.directors"
                    :key="j">{{dir.name}}</span></p>
            <p class="movie-casts">主演：<span v-for="(casts,k) in movie.casts"
                    :key="k">{{casts.name}}</span></p>
            <div class="movie-genres">
              <span v-for="(genres,l) in movie.genres"
                    :key="l">{{genres}}</span>
            </div>
            <div class="star">
              <van-rate v-model="starSort[i]"
                        color="#ffd21e"
                        allow-half
                        void-icon="star" />
              <span class="score">{{movie.rating.average | fullScore}}</span>
            </div>
          </div>
        </router-link>
      </div>
      <van-loading v-show="loading"/>
      <footer v-show="!loading">
        <span>已经到底了哟</span>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      tipShow: true,
      suggestList: [],
      searchData: [],
      totalValue: 1,
      startValue: 0,
      countValue: 10,
      value: 4.5,
      starSort: [],
      switchData: true,
      loading: true,
      findSort: {
        name: "",
        switch: false
      }
    };
  },
  created() {
    // 推荐搜索: item
    this.randomSuggest();
    if(this.$route.params.name){
      this.findSortName();
    }
    console.log("已取值："+this.$route.params.name);
  },
  mounted() {
    let search = this.$refs.searchBox.getElementsByTagName("input")[0];
    search.value = this.$store.state.searchValue;
    this.switchData = false;
    window.addEventListener("scroll", () => {
      /**
       * 浏览器滚动到底部继续获取数据。
       */
      let bottomOfWindow =
        document.body.offsetHeight - window.scrollY - window.innerHeight <= 200;
      if (bottomOfWindow && this.switchData) {
        this.switchData = false;
        // 开始获取数据
        this.onSearch();
        console.log("到底了");
      }
    });
  },
  watch: {
    startValue(val){
      /**
       * @val startValue 即请求的数据开始索引
       * val 如果大于或等于 total 即表示数据请求完毕，loading 动画关闭。
       */
      if(val >= this.totalValue){
        this.loading = false;
      }
    },
    '$route':{
      handler(route){
        /**
         * 方法说明：
         * 在发现页面点击分类卡片跳转到搜索页面进行搜索。
         * 如果路由发生了变化，且跳转到 Search 的路由时判断 params 是否有值，进行搜索
         */
        if(route.name === "Search"){
        // const that = this;
          if(this.$route.params.name){
            this.findSortName();
          }
        }
      }
    }
  },
  methods: {
    onReset(){
      /**
       * 只要搜索框中的值发生了改变，清空所有内容。
       */
      this.tipShow = true;
      this.searchData = [];
      this.totalValue = 1;
      this.startValue = 0;
      this.starSort = [];
    },
    onSearch() {
      /**
       * @API_PROXY GitHub https://github.com/xCss/JsonBird 远程数据接口代理服务。
       * @URL 根据搜索框内的值切换 URL 地址：分类URL、搜索URL
       */
      this.$store.commit("saveSearchVal");
      this.tipShow = false;
      const API_PROXY = "https://bird.ioliu.cn/v1/?url=";
      if(this.$store.state.movieSort.includes(this.$store.state.searchValue)){
        URL = "https://api.douban.com/v2/movie/search?tag=";
      }else{
        URL = "https://api.douban.com/v2/movie/search?q=";
      }
      if (/^[0-9a-zA-Z_.-[\u4e00-\u9fa5]/.test(this.$store.state.searchValue)) {
        // 只支持中文及英文搜索，为空/空格将不能搜索
        this.$ajax
          // .get("/xiju")
          .get(API_PROXY + URL + this.$store.state.searchValue + "&start=" + this.startValue + "&count=" + this.countValue)
          .then(response => {
            /**
             * 2019-05-11T04:16:23.732Z
             * 方法说明：
             * 1、获取数据
             * 2、start：数据开始索引，count 数据条数
             * 3、将数据总条数 total 存储到 this.total 中（默认 start = 0；total = 1）
             * 4、如果 start 大于 total 则说明数据已全部获取完毕，需要立刻结束。
             * 5、否则继续请求数据，因为数据是一段一段获取的，所以每一次都是 push 到 this.searchData 中。
             */

            // @mock 使用
            // this.totalValue = response.data.data.total;
            // this.searchData = response.data.data.subjects;
            // for (let i = 0; i < response.data.data.subjects.length; i++) {
            //   this.starSort.push((response.data.data.subjects[i].rating.average)/2);
            // }

            // 真实获取请求
            this.totalValue = response.data.total;
            if (this.startValue >= this.totalValue) {
              this.switchData = false;
              return false;
            }
            for (let i = 0; i < this.countValue; i++) {
              this.searchData.push(response.data.subjects[i]);
              this.starSort.push((response.data.subjects[i].rating.average)/2);
            }
            this.startValue += this.countValue;
            this.switchData = true;
          })
          .catch(error => {
            this.$toast("获取数据失败，请稍候再试。");
          });
      } else {
        this.$toast("换个搜索词试试吧~");
      }
    },
    onCancel() {
      /**
       * 点击取消按钮，清空所有内容并返回上一级。
       */
      this.$store.commit("cancelSearch");
      this.onReset();
      this.findSort.name = "";
      console.log(this.findSort);
      this.$router.go(-1);
    },
    searchItem(e) {
      /**
       * @e 推荐搜索下的分类按钮。
       * 功能：
       * 获取点击的对象的文本，把它键入搜索框内，并搜索
       */
      let value = e.toElement.innerText;
      this.$store.commit("searchItem", value);
      this.onSearch();
    },
    focus() {
      /**
       * @Element 搜索框
       * 搜索框获取焦点以后：提示面板显示，搜索结果隐藏。
       * 原因：需要历史记录提示等功能。
       * 主要原因：搜索框绑定了搜索结果的标题（以下是xxx的搜索结果：）
       *         其中的“xxx”为双向绑定，文本框改变它也改变，但是确不能立即搜索（节省请求次数）。
       */
      this.tipShow = true;
    },
    randomSuggest() {
      /**
       * 功能：
       * 产生随机数，把 “this.$store.state.movieSort” 中的五个值渲染到搜索提示/推荐面板。
       * 产生一种数据实时更新的假象。|ﾟДﾟ))) 居然是假的！！
       */
      for (let i = 0; i < 5; i++) {
        let randomNum = Math.round(Math.random() * this.$store.state.movieSort.length - 1);
        let item = this.$store.state.movieSort.splice(randomNum, 1);
        this.suggestList.push(item.join(","));
      }
    },
    findSortName(){
      this.findSort.name = this.$route.params.name;
      this.findSort.switch = true;
      this.$store.commit("searchItem", this.findSort.name);
      this.onSearch();
      this.findSort.name = "";
    }
  }
};
</script>

<style scoped>
.container {
  padding-top: 63px;
}
p {
  margin: 0;
}
h6 {
  margin: 0;
  padding: 0;
}
.search-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 45px;
  z-index: 5;
}
.search-bar {
  background-color: #fff;
  border-bottom: 0.02667rem solid #efefef;
}
.search-label {
  font-size: 16px;
}
.search-content .search-label {
  margin-bottom: 20px;
}
.keyword {
  color: #fec827;
}
.suggest .suggest-item {
  padding: 5px 0;
  font-size: 12px;
}
.suggest-item:not(:last-child) {
  margin-bottom: 10px;
}
.suggest-item span,
.search-movie-card .movie-genres span {
  display: inline-block;
  margin: 0 8px 10px 0;
  padding: 0 14px;
  color: #808080;
  line-height: 28px;
  font-weight: bold;
  border-radius: 100px;
  background-color: #f0f2f5;
}
.suggest-item span:active {
  background: #d8d8d8;
}

.search-movie-card {
  display: flex;
  align-content: flex-start;
  box-sizing: border-box;
  position: relative;
  border-radius: 5px;
  line-height: 0;
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 11 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.826 15.449c.174.248.223.565.123.857l-.11.224-8.95 14.981c-.279.468-.898.628-1.382.358-.484-.27-.65-.868-.37-1.336l8.681-14.532-8.682-14.534c-.279-.468-.114-1.066.37-1.336s1.103-.11 1.382.358l8.937 14.96z' fill='%23D8D8D8'/%3E%3C/svg%3E")
    no-repeat right center;
  background-size: 8px;
}
.search-movie-card a {
  display: flex;
}
.search-movie-card:not(:last-child):after {
  content: "";
  position: absolute;
  bottom: -14px;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #efefef;
}
.search-movie-card .movie-card-cover {
  margin-right: 10px;
  width: 96px;
  align-self: flex-start;
  border-radius: 5px;
}
.movie-text-info {
  display: flex;
  align-self: center;
  /* flex-flow: row wrap; */
  flex-direction: column;
  flex: auto;
  /* vertical-align: middle; */
  line-height: 0;
}
.search-movie-card:not(:last-child) {
  margin-bottom: 30px;
}
.search-movie-card .movie-title {
  margin-bottom: 4px;
  font-size: 18px;
}
.search-movie-card .movie-dir,
.movie-casts {
  margin: 0;
  padding-right: 44px;
  line-height: 1;
  font-size: 14px;
  opacity: 0.5;
  width: 170px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.search-movie-card .movie-dir span:not(:last-child)::after,
.movie-casts span:not(:last-child)::after {
  content: "、";
}
.search-movie-card .movie-genres {
  padding: 6px 0;
  line-height: 0;
}
.search-movie-card .movie-genres span {
  margin-bottom: 0;
  padding: 0 12px;
  font-size: 12px;
  line-height: 26px;
}
.star {
  line-height: 0;
  height: 20px;
}
.star .van-rate__item,
.star .van-rate {
  line-height: 0;
}
.star .van-rate {
  display: inline-block;
  vertical-align: top;
}
.star .score {
  display: inline-block;
  margin-left: -4px;
  color: #fec827;
  line-height: 20px;
  font-size: 18px;
  font-weight: bold;
}

.van-loading{
  top: 0;
  margin-bottom: 15px;
}
</style>


