// https://api.douban.com/v2/movie/in_theaters?city=南昌&star=0 正在热映，区分地区
// https://api.douban.com/v2/movie/coming_soon 即将上映，不区分地区
// https://api.douban.com/v2/movie/subject/:id  电影详情 ":id" 替换成电影 ID
// https://api.douban.com/v2/movie/coming_soon?city=南昌&start=0&count=10
var _searchArr_ = JSON.parse(localStorage.getItem('historySearch') || '[]');
export default {
  title: "state",
  city: "",
  homeURL: "https://api.douban.com/v2/movie/in_theaters?city=北京&start=",
  maskStatus: false,
  homeSortCard: { type: "hot", name: "正在热映" },
  searchValue: "",
  historySearch: _searchArr_,
  movieSort: [
    "喜剧",
    "动作",
    "爱情",
    "科幻",
    "动画",
    "纪录片",
    "悬疑",
    "犯罪",
    "奇幻",
    "歌舞",
    "同性"
  ]
}


